.boxx {
    width: 200px;
    height: 200px;
    border: 1px solid black;
    position: relative;
  }
  
  .water {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0;
    overflow: hidden;
  }
  
  .wave-animation {
    height: 20px;
    width: 100%;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.1) 50%, rgba(255, 255, 255, 0.1) 100%);
    background-repeat: repeat-x;
    animation: wave 2
  }  