.outer-box_a {
  background-color: rgb(136, 142, 240);
  width:12%;
  align-items: center;
  border-radius: 20px;
  justify-content: center;
}
.sup_a {
  position: absolute;
  top: 39%;
  align-items: center;
  align-items: bottom;
  left: 48%;
  width: 60%;
  height: 10%;
  display: flex;
  /* border: 5px solid #f9f9f9; */
  display: flex;
  z-index: 6;
}

.title_a {
  font-size: 18px;
  font-weight: bold;
  align-items: center;
  left: 12%;
  position: relative;
}


.bottom-left-box {
  width: 50%;
  height: 60%;
  background-color: transparent;
  border-radius: 20px;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}


.bottom-right-box {
  width: 50%;
  height: 60%;
  float: right;
  border-radius: 20px;
  display: flex;
  background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

