.outer-box_r_a {
    background-color: rgb(184, 248, 174);
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);

    transition: background-color 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    z-index: 5;
  }
  .outer-box_r_a:hover {
    transform: scale(1.1);
  }
  .boxes_r_a {
    z-index: 6;
    position: absolute;
    height: 10%;
    top: 77%;
    text-align: center;
    width: 8%;
    
    left: 23%;
  }
.sup_r_a {
    display: grid;    
    height: 100%;
    width: 100%;
  }
  .title_r_a {
    font-size: 16px;
    font-weight: bold;
    align-items: center;
    /* left: 22%; */
    position: relative;
  }
  .bottom-left-box_r_a {
    width: 50%;
    height: 60%;
    background-color: transparent;
    border-radius: 20px;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .bottom-right-box_r_a {
    width: 50%;
    height: 60%;
    font-size: larger;
    float: right;
    border-radius: 20px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);

    display: flex;
    background: rgba(255, 255, 255, 0.5);
      backdrop-filter: blur(5px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

