.parent-container {
    z-index: 10;
    top: 0;
    left: 10%;
    width: 100%;
    height: 1%;
    display: flex;
    
  }
  
  .date-picker-wrapper {
    background-color: lightblue;
    padding: 10px;
    width: 100px;
    text-align: center;
  }
  
  .date-picker {
    width: 50%;
  }
  /* Custom styles for the date picker */

/* Adjust the date picker container */
.custom-datepicker {
  font-family: Arial, sans-serif;
  font-size: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  background-color: #fff;
  color: #333;
  width: 40%;
}

/* Adjust the input field */
.custom-datepicker__input {
  height: 30px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Adjust the calendar popup */
.custom-datepicker__calendar {
  margin-top: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border: 1px solid #ccc;
  background-color: #fff;
}

/* Adjust the navigation buttons */
.custom-datepicker__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  background-color: #f0f0f0;
  border-bottom: 1px solid #ccc;
}

/* Adjust the month and year dropdowns */
.custom-datepicker__navigation-select {
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: #333;
}

/* Adjust the weekdays */
.custom-datepicker__day-name {
  font-weight: bold;
}

/* Adjust the days in the calendar */
.custom-datepicker__day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  font-size: 14px;
  color: #333;
}

/* Adjust the selected day */
.custom-datepicker__day--selected {
  background-color: #007bff;
  color: #fff;
}

/* Adjust the disabled days */
.custom-datepicker__day--disabled {
  color: #ccc;
  cursor: not-allowed;
}

/* Adjust the today indicator */
.custom-datepicker__day--today {
  border: 2px solid #007bff;
  border-radius: 50%;
}

/* Adjust the day outside the current month */
.custom-datepicker__day--outside-month {
  color: #999;
}
