/* Box Styles */
.boxe {
    display: inline-block;
    margin: 10px;
    border-radius: 20px;
    /* background-color: #a8ff78c9; */
    background-color: #ecf3f1;
    z-index: 4;
    transition: background-color 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    align-items: top;
    justify-content: top;
    justify-items: top;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    width: 100%;

  }
  .boxe:hover {
    background-color: #ffffff;
    transform: scale(1.1);
  }
  
  .box-header {
    text-align: center;
    margin-bottom: 10px;
  }
  
  .box-inner {
    display: flex;
    justify-content: center;
    bottom: 1%;
    height: 40%;
    top:80%;
  }
  
  .box-data {
    display: flex;
    bottom: 1%;
  }
  
  .box-data-left {
    flex: 1;
    margin-right: 15px;
    bottom: 1%;
    top: 10px;
  }
  
  .box-data-right {
    flex: 1;
    margin-left: 15px;
  }
  
  .smal-box {
    /* border: 5px solid #fffdfd; */
    background-color: transparent;
    /* border-radius: 20px  #ccc; */
    padding: 5px;
    background-color: #d0f2bec9;
    /* background-image: url('C:/Users/Chetan/Downloads/pp.jpg'); */
    background-size: cover;
    transition: transform 0.2s ease-in-out;
    align-Items: 'flex-start';
    height: 40%;
  }
 
  .small-box {
    /* border: 1px solid #ccc; */
    justify-content: space-between;
    /* top: 4%; */
    /* width: 100%; */
    text-align: center;
    background-color: transparent;
    border-radius: 20px;
    /* padding: 5px; */
    background-color: transparent;
    transition: transform 0.2s ease-in-out;
  }
  .small-box:hover {
    transform: scale(1.1);
  }
  
  .box-data p {
    margin: 0;
    font-size:large;
  }
  
  .box-data-left p {
    text-align: left;
    color: #545252;
    font-size:large;
    text-align: center
  }
  
  .box-data-right p {
    font-size:large;
    text-align: right;
    text-align: center;
    color: #3c3b3b;
    
  }
  .boxe h3 {
    font-size: medium;
    justify-content: top;
    align-items: top;
    text-align: center;
  }
  