.date-picker-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
  
  .date-picker-label {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .date-picker-input {
    padding: 5px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 3px;
    margin-bottom: 10px;
  }
  
  .date-picker-submit {
    padding: 8px 12px;
    font-size: 1rem;
    border: none;
    border-radius: 3px;
    background-color: #0077cc;
    color: #fff;
    cursor: pointer;
  }
  
  .date-picker-submit:hover {
    background-color: #0066b2;
  }
  