.outer-box_m_a {
  width:100%;
  align-items: center;
  border-radius: 20px;
  justify-content: center;
}

.meter_m_a {
  position: absolute;
  top: 47%;
  align-items: center;
  align-items: bottom;
  padding: 10px;
  left: 72.5%;
  width: 15%;
  height: 10%;
  border-radius: 20px;
  box-shadow: 0 0 2px rgba(209, 203, 203, 0.3);
  display: flex;
  /* border: 5px solid #a8ff78c9; */
  display: flex;
  z-index: 6;
}
.title_m_a {
  font-size: 20px;
  font-weight: bold;
  align-items: center;
  left: 40%;
  position: relative;
  color: rgb(54, 73, 57);
}
.bottom-left-box_m_a {
  width: 33%;
  height: 60%;
  background-color: transparent;
  border-radius: 12px;
  float: left;
  display: flex;
  color: rgb(97, 100, 100);
  transition: transform 0.2s;
  transform: scale(1.1);
  justify-content: center;
  align-items: center;
}
.bottom-left-box_m_a:hover {
  transform: scale(1.1);
}

.bottom-right-box_m_a {
  width: 33%;
  height: 60%;
  font-size:larger;
  float: right;
  border-radius: 20px;
  display: flex;
  background: transparent;
    /* backdrop-filter: blur(5px); */
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

