.outer-delta {
  background-color: rgb(240 195 136);
  width:12%;
  align-items: center;
  border-radius: 20px;
  justify-content: center;
}
.delta_t {
  position: absolute;
  top: 58%;
  align-items: center;
  align-items: bottom;
  left: 45%;
  width: 60%;
  height: 10%;
  display: flex;
  /* border: 5px solid #f9f9f9; */
  display: flex;
  z-index: 6;
}

.title_delta {
  font-size: 26px;
  font-weight: bold;
  align-items: center;
  left: 38%;
  position: relative;
}


.bottom-left-box_delta {
  width: 50%;
  height: 60%;
  background-color: transparent;
  border-radius: 20px;
  float: left;
  display: flex;
  justify-content: center;
  font-size: larger;
  align-items: center;
}


.bottom-right-box_delta {
  width: 100%;
  height: 60%;
  font-size: larger;
  float: right;
  border-radius: 20px;
  display: flex;
  background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

